import React, { useState } from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import { useTheme } from '@material-ui/core/styles';
import BuyAxionAuctionModal from './BuyAxionAuctionModal';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { Typography, AppBar, Toolbar, IconButton, Grid, Menu, MenuItem, Button } from '@material-ui/core';

const Header = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [modalOpen, setModalOpen] = useState(false);

    const theme = useTheme();
    const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

    const openWebsite = () => window.open("https://axion.network", "_blank");
    const openReferral = () => {
        setModalOpen(true);
        setAnchorEl(null)
    }

    const handleClick = (event) => { setAnchorEl(event.currentTarget) }
    const handleClose = () => { setAnchorEl(null) }

    return (
        <div>
            <AppBar position="static" className="navBar" elevation={10} >
                <Toolbar>

                    <Grid container justify="space-between" alignItems="center">
                        <Grid item>
                            <Typography variant="h5" align="center" color="primary" style={{ fontWeight: '100', color: "#FFF" }}>Axion Stats</Typography>
                        </Grid>
                        <Grid item>
                            {!isSmall && (
                                <div className="headerLinks">
                                    <Typography onClick={openWebsite} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink noselect">Axion Website</Typography>

                                    <Button onClick={openReferral} variant="contained" color="secondary" className="referralButton">
                                        <Typography variant="subtitle2">Buy Axion, Get 10% Bonus</Typography>
                                    </Button>
                                </div>
                            )}

                            {isSmall && (<>
                                <IconButton edge="start" color="inherit" onClick={handleClick}>
                                    <MenuIcon />
                                </IconButton>

                                <Menu anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose} style={{padding: 0, margin: 0}}>
                                    <MenuItem onClick={openWebsite}>Axion Website</MenuItem>
                                    <MenuItem onClick={openReferral}>Buy Axion - 10% Bonus</MenuItem>
                                </Menu>
                            </>)}
                        </Grid>
                    </Grid>

                </Toolbar>
            </AppBar>

            <BuyAxionAuctionModal isOpen={modalOpen} close={() => setModalOpen(false)}/>
        </div>
    )
}

export default Header;