import React from 'react';
import { Card, CircularProgress, Typography } from "@material-ui/core";
import CardBG from '../../assets/img/card_bg.jpg';

const StatCard = ({ stat, amount, loading, suffix }) => {

    return(
        <Card className="card" elevation={6}>
            <img src={CardBG} width="100%" className="cardBG" alt="card background"/>

            <div style={{ textAlign: 'center', padding: '1%' }}>           
                <div>
                    <Typography variant="h6" color="secondary" style={{ fontWeight: '400' }}>{stat}</Typography>
                    {loading ? <CircularProgress size={23} /> : (
                        <Typography variant="subtitle1" color="secondary" style={{ fontWeight: '400' }}>{amount} {suffix}</Typography>
                    )}
                    
                </div>
            </div>
          
        </Card>
    )
}

export default StatCard;