import React from 'react';
import { Grid, Typography, Tooltip } from '@material-ui/core';
import useHolderData from '../../hooks/useHolderData';
import StatCard from './EcosystemStatCard.js';
import TigerShark from '../../assets/img/tigershark.png';
import Shrip from '../../assets/img/shrimp.png';
import moment from 'moment';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';

const EcosystemBreakdown = () => {
    const { currentData , currentDataLoading } = useHolderData();
    
    const ecosystem = [
        { key: "shrimp", type: "Shrimp", count: currentData?.shrimp?.count, img: true, src: Shrip, width: 30, totalAXN: currentData?.shrimp?.totalAxn, color: '#e6194B' },
        { key: "crab", type: "Crabs", count: currentData?.crab?.count, emoji: "ü¶Ä", totalAXN: currentData?.crab?.totalAxn, color: '#f58231'  },
        { key: "fish", type: "Fish", count: currentData?.fish?.count, emoji: "üê†", totalAXN: currentData?.fish?.totalAxn, color: '#ffe119'  },
        { key: "octopus", type: "Octopuses", count: currentData?.octopus?.count, emoji: "üêô", totalAXN: currentData?.octopus?.totalAxn, color: '#bfef45' },
        { key: "dolphin", type: "Dolphins", count: currentData?.dolphin?.count, emoji: "üê¨", totalAXN: currentData?.dolphin?.totalAxn, color: '#3cb44b'  },
        { key: "tigerShark", type: "Tiger Sharks", count: currentData?.tigerShark?.count, img: true, src: TigerShark, width: 50, totalAXN: currentData?.tigerShark?.totalAxn, color: '#42d4f4' },
        { key: "greatWhite", type: "Great Whites", count: currentData?.greatWhite?.count, emoji: "ü¶à", totalAXN: currentData?.greatWhite?.totalAxn, color: '#4363d8'  },
        { key: "whale", type: "Whales", count: currentData?.whale?.count, emoji: "üê≥", totalAXN: currentData?.whale?.totalAxn, color: '#911eb4'  },
    ]

    return (
        <div style={{padding: '2%'}}>
            <Typography variant="h4" align="center" color="primary" style={{ fontWeight: '100' }}>
                Axion Ecosystem  
                {!currentDataLoading && currentData.totals && (
                   <sup>
                        <Tooltip title={`Last Updated: ${moment(currentData.totals.last_updated).format("MMM Do, YYYY, h:mm a")}. Ecosystem is updated every 10 minutes.`} classes={{ tooltip: 'tooltip' }} placement="right">
                            <InfoOutlinedIcon style={{ fontSize: '0.7rem', cursor: 'pointer' }} color="primary" />
                        </Tooltip>
                   </sup>
                )}
            </Typography>

            {!currentDataLoading && currentData.totals && (
                <>
                    <Typography variant="subtitle1" align="center" color="primary" style={{ fontWeight: '100' }}>{currentData.totals.holders.toLocaleString()} addresses hold at least 1 AXN.</Typography>
                    
                    <Tooltip title="Does not include contracts or dev fund" classes={{ tooltip: 'tooltip' }} placement="bottom">
                        <Typography variant="subtitle1" align="center" color="primary" style={{ fontWeight: '100', maxWidth: '325px', margin: '0 auto', cursor: 'pointer' }}>
                            Ecosystem Total: {Math.round(currentData.totals.held_axn).toLocaleString()} AXN
                        </Typography>
                    </Tooltip>
                </>
            )}
          
            <div style={{margin: '2%'}}>
                <Grid container justify="space-evenly" spacing={3}>
                    {ecosystem.map(e => (
                        <Grid item lg={3} md={6} xs={10} key={e.type}>
                            <StatCard 
                                type={e.type} 
                                count={e.count} 
                                loading={currentDataLoading} 
                                emoji={e.emoji} 
                                isImage={e.img ?? false} 
                                src={e.img ? e.src : null} 
                                imgWidth={e.width} 
                                total={e.totalAXN} 
                                ecoAxion={currentData?.totals?.held_axn} 
                                holders={currentData?.totals?.holders}
                            />
                        </Grid>
                    ))}
                </Grid>

            </div>
        </div>
    )
}

export default EcosystemBreakdown;