import React from 'react';
import CloseIcon from '@material-ui/icons/Close';
import { Button, Dialog, DialogContent, Divider, IconButton, Typography } from '@material-ui/core';

const BuyAxionAuctionModal = ({ isOpen, close }) => {
    const openUniswap = () => window.open("https://app.uniswap.org/#/swap?outputCurrency=0x7d85e23014f84e6e21d5663acd8751bef3562352", "_blank");
    const openReferral = () => window.open("https://stake.axion.network/auction?ref=0xbE42d298d31b2551aE9E6e88B838A3ba5Dc1D6CD", "_blank");

    return (
        <Dialog open={isOpen} onClose={close}>
            <div style={{ margin: '1%', padding: '2%' }}>
                <Typography variant="h6" color="primary">Axion's 10% Referral Bonus</Typography>

                <div style={{ position: 'absolute', top: "5px", right: "10px"}}>
                    <IconButton onClick={close}>
                        <CloseIcon/>
                    </IconButton>
                </div>
            </div>

            <DialogContent>
                <Typography variant="subtitle1" color="textSecondary">By purchasing Axion from the daily auction using my link, you will get a <strong>10% bonus</strong>. Axion from the auction will be automatically staked for 14 days and you must manually claim it afterwards.</Typography>
                <br />
                <Typography variant="subtitle1" color="textSecondary">Example: If you receive 1,000,000 AXN from the auction, you will earn a bonus of 100,000 AXN. You will get the total of 1,100,000 AXN along with any rewards from the stake after you claim it.</Typography>
            </DialogContent>
            
            <Divider style={{margin:'3% auto', width: '90%'}} />

            <div style={{margin: '1% auto', width: '85%'}}>
                <Button fullWidth variant="contained" color="primary" onClick={openReferral} autoFocus style={{ textTransform: 'none' }}>
                    Buy From Auction &amp; Get 10% Bonus AXN
                </Button>
                <Button fullWidth onClick={openUniswap} color="secondary" style={{textTransform: 'none'}}>Buy From Uniswap Instead, Instantly</Button>
            </div>
        </Dialog>
    )
}

export default BuyAxionAuctionModal;