import React from 'react';
import moment from 'moment';
import StatCard from './MarketStatCard.js';
import useMarketData from '../../hooks/useMarketData';
import { Grid, Switch, Typography, FormControlLabel } from '@material-ui/core';
import Logo from '../../assets/img/logo.png';

const MarketStats = () => {
    const { 
        volumeUsd,
        marketCap,
        axnPerEth, 
        usdtPerAxn, 
        lastUpdated,
        autoUpdating,
        toggleAutoUpdating
    } = useMarketData();

    return (
        <div style={{ padding: '2%', paddingBottom: 0 }}>

            <center>
                <img src={Logo} width={60} alt="logo"/>
            </center>
            
            <Typography variant="h4" align="center" color="primary" style={{ fontWeight: '100' }}>Axion Market Stats</Typography>

            <center>
                <FormControlLabel labelPlacement="start" control={<Switch size="small" checked={autoUpdating} onChange={toggleAutoUpdating} />} label={<Typography variant="subtitle2" color="textSecondary" style={{ fontWeight: '400' }}>Automatically update stats</Typography>} />
                <br/>

                {lastUpdated && (
                    <Typography variant="subtitle2" color="textSecondary" style={{ fontWeight: '400' }}>
                        Last Updated: {moment(lastUpdated).format("h:mm:ss a")}
                    </Typography>
                )}
            </center>

            <div style={{ margin: '2%' }}>
                <Grid container justify="space-evenly" spacing={3}>
                    <Grid item lg={3} md={6} xs={10} key="USDTPrice">
                        <StatCard loading={Boolean(!usdtPerAxn)} stat="Current Price" amount={`$${usdtPerAxn}`} suffix="USDT" />
                    </Grid>
                    <Grid item lg={3} md={6} xs={10} key="pricePerETH">
                        <StatCard loading={Boolean(!axnPerEth)} stat="Axion Per ETH" amount={Number(axnPerEth).toLocaleString()} suffix="AXN"/>
                    </Grid>
                    <Grid item lg={3} md={6} xs={10} key="marketCap">
                        <StatCard loading={Boolean(!marketCap)} stat="Market Cap" amount={`${marketCap.toLocaleString("en-US", { style: "currency", currency: "USD" })}`} suffix="USD" />
                    </Grid>
                    <Grid item lg={3} md={6} xs={10} key="volumeETH">
                        <StatCard loading={Boolean(!volumeUsd)} stat="24h Volume" amount={Number(volumeUsd).toLocaleString("en-US", { style: "currency", currency: "USD" })} suffix="USD"/>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default MarketStats;